<template>
	<view>
		<view class="mt-2 ml-2">
		    <label for="mt-3">用户名：</label>
		    
			<input type="text" v-model="username" name="username" id="username" class="form-control" placeholder="取个独特的名字吧" >
	
			<view class="d-flex mb-5">
				<button id="connect" class="btn btn-default" @click="login" type="submit">连接</button>
				<button id="disconnect" class="btn btn-default" type="submit" >断开</button>
			</view>
		
		
			<view @click="switchUser(item)" class="d-flex mt-5" v-for="(item,index) in userlist" :key="index">
				<view class="mr-4">
					在线用户
				</view>
				<view class="">
					{{item}}
				</view>
				
			</view>
			<view class="input-group" style="height: 74px">
			    <textarea v-model="content" class="form-control" rows="3" style="resize: none;" placeholder="单击一名在线用户，与其聊天吧...." ></textarea>
			    <span class="input-group-btn">
			    <button @click="sendToUser" class="btn btn-default" type="submit" style="height: 100%">发送</button>
			    </span>
			</view>
			
			
		
			
		</view>
		   
		
	</view>
</template>

<script>
	import Stomp from 'stompjs'
	import SockJS from "sockjs-client";
	
	var stompClient = null
	export default {
		data() {
			return {
				username: '',
				userlist: [],
				content: '',
				switch:'',
		
				
				   
			}
		},
		created() {
			this.getUserList();
			
		},
		methods: {
			switchUser(e){
				this.switch = e
				console.log(this.switch)
				
			},
			sendToUser(){
				console.log("sendToUser")
				stompClient.send("/app/private", {}, 
			    JSON.stringify({'name': this.username, 'content': this.content, 'receiver': this.switch}));
			},
			connectWeb() {
			  var sessionId = null
			  const socket = new SockJS('http://localhost:8080/gs-guide-websocket');
			  stompClient = Stomp.over(socket);
			  stompClient.connect(
			    {},
			    (frame) => {
					
					sessionId = /\/([^\/]+)\/websocket/.exec(socket._transport.url)[1];
					//接下来写一写关于订阅接口的事情
			       
					stompClient.subscribe('/topic/greetings', function (greeting) {
					      console.log(JSON.parse(greeting.body).content)
						  
					    });
					stompClient.subscribe('/user/topic/private', function (greeting) {
					      var parse = JSON.parse(greeting.body);
						  console.log(parse)
					    
					    });
					 stompClient.subscribe('/topic/userlist', function (greeting) {
					      var parse = JSON.parse(greeting.body);
						  console.log(parse)
					    });
			    },
			    (error) => {
			        console.error('error', error)
			    }
			  );
			},
			getUserList(){
				var that = this;
				uni.request({
					
					url:'http://localhost:8080/userlist',
					success(res) {
						that.userlist = res.data
						console.log(res)
					}
				})
				
			},
			login(){
				var that = this;
			
				uni.request({
					url:'http://localhost:8080/login2',
					method: 'POST',
					header: { 
							'content-type': 'application/x-www-form-urlencoded'
					},
					data: {'username': that.username},
					success(res) {
						console.log("success")
						console.log(res.data)
						that.connectWeb();
						
						
					},
					fail(res) {
						console.log("fail")
						console.log(res.data)
					}
				})
			}
			
		}
	}
</script>

